<template>
  <div>
    <div class="title">
      <div style="width:25rem;display:flex">
        <el-button :type="item.type" v-for="(item,index) in levelData" :key="index" @click="changeLevel(item)">{{item.name}}</el-button>
      </div>
      <div style="width:25rem;text-align:center">
        最佳:{{record[nowLevel]}}
      </div>
      <div style="width:25rem;display:flex">
        <div style="width:10rem">时间:{{nowTime}}</div>
        <div style="width:15rem">未标记的地雷:{{leftMine}}</div>
      </div>
    </div>
    <div class="minesweeper">
      <div class="mines-row" v-for="(row,rowIndex) in mineDate" :key="rowIndex">
        <div v-for="(item,columnIndex) in row" :key="columnIndex">
          <Mine :state.sync="item.state" :number="item.number" :isMine="item.isMine" @open="handleOpen(columnIndex,rowIndex)" @openRoundByFlag="openRoundByFlag(columnIndex,rowIndex,item)" @changeFlag="hanldeChangeFlag" />
        </div>
      </div>
    </div>
    <el-dialog :visible.sync="loseDialogVisible" width="30%" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
      <div class="center-box">
        <Boom />
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="warning" @click="restart();loseDialogVisible = false">重新开始当前游戏</el-button>
        <el-button type="primary" @click="setMineDate();loseDialogVisible = false">开始新游戏</el-button>
      </span>
    </el-dialog>
    <el-dialog :visible.sync="winDialogVisible" width="50%" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
      <div class="center-box">
        <div style="font-size:2rem" v-show="newRecord">新纪录</div>
        <div style="font-size:2rem">时间:{{nowTime}}秒</div>
        <div v-show="nowLevel===3" class="good-box">
          <Good style="width:8rem" />
          <span style="font-size:5rem">猴哥的认可</span>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="setMineDate();winDialogVisible = false">开始新游戏</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Mine from './mine'
import Boom from '@/components/SVG/boom'
import Good from '@/components/SVG/good'
export default {
  components: { Mine, Boom, Good },
  data () {
    return {
      newRecord: false,
      record: { 0: 9999, 1: 9999, 2: 9999, 3: 9999 },
      allFlagNumber: 0,//插旗的数量
      nowTime: 0,
      timer: null,
      nowLevel: 0,
      loseDialogVisible: false,
      winDialogVisible: false,
      state: 1,
      width: 9,//x列
      height: 9,//y行
      mineNumber: 10,//雷数量
      mineDate: [],
      start: false,
      leftOpen: 71,
      levelData: [
        { name: '初级', type: 'success', width: 9, height: 9, mineNumber: 10, level: 0 },
        { name: '中级', type: 'primary', width: 16, height: 16, mineNumber: 50, level: 1 },
        { name: '高级', type: 'warning', width: 30, height: 16, mineNumber: 99, level: 2 },
        { name: '宝叔级', type: 'danger', width: 45, height: 20, mineNumber: 199, level: 3 },
      ]
    }
  },
  created () {
    this.setMineDate()
    if (localStorage.record) {
      this.record = JSON.parse(localStorage.record)
    }
  },
  computed: {
    leftMine () {
      return this.mineNumber - this.allFlagNumber
    }
  },
  methods: {
    //改变等级
    changeLevel (item) {
      this.nowLevel = item.level
      this.width = item.width
      this.height = item.height
      this.mineNumber = item.mineNumber
      this.setMineDate()
    },
    //初始化无雷
    setMineDate () {
      this.newRecord = false
      this.nowTime = 0
      this.allFlagNumber = 0
      this.mineDate = []
      this.start = false
      this.leftOpen = this.width * this.height - this.mineNumber
      for (let i = 0; i < this.height; i++) {
        const row = []
        for (let j = 0; j < this.width; j++) {
          row.push({ isMine: false, state: 1, number: 0 })
        }
        this.mineDate.push(row)
      }
    },
    //布雷
    setMine (x, y) {
      const roundPosition = this.getRoundPosition(x, y).filter(position => this.getItem(...position))
      //获取第一次点击周围的位置不给雷 自己也不是雷
      let undivideMineNumber = this.mineNumber
      while (undivideMineNumber) {
        let randomX = Math.floor(Math.random() * this.width)
        let randomY = Math.floor(Math.random() * this.height)
        if (
          !this.mineDate[randomY][randomX].isMine &&
          !roundPosition.find(position => position[0] === randomX && position[1] === randomY) &&
          !(randomX === x && randomY === y)
        ) {
          this.mineDate[randomY][randomX].isMine = true
          undivideMineNumber--
        }
      }
    },
    //获取数字
    setNumber () {
      for (let i = 0; i < this.height; i++) {
        for (let j = 0; j < this.width; j++) {
          let number = this.getRoundItems(j, i).reduce((a, b) => a + Number(b.isMine), 0)
          this.mineDate[i][j].number = number
        }
      }
    },
    //获取周围
    getRoundPosition (x, y) {
      return [[x - 1, y - 1], [x, y - 1], [x + 1, y - 1], [x - 1, y], [x + 1, y], [x - 1, y + 1], [x, y + 1], [x + 1, y + 1]]
    },
    //获取Item
    getItem (x, y) {
      if (!this.mineDate[y]) return
      if (!this.mineDate[y][x]) return
      return this.mineDate[y][x]
    },
    //获取周围的item
    getRoundItems (x, y) {
      let roundPosition = this.getRoundPosition(x, y)
      let roundItems = []
      for (let i = 0; i < roundPosition.length; i++) {
        let item = this.getItem(...roundPosition[i])
        if (item) {
          roundItems.push(item)
        }
      }
      return roundItems
    },
    //打开
    handleOpen (x, y) {
      if (!this.timer) {
        this.timer = setInterval(() => {
          this.nowTime++
        }, 1000)
      }
      if (!this.start) {//第一次点击
        this.start = true
        this.firstOpen(x, y)
      }
      //失败
      if (this.mineDate[y][x].isMine) {
        this.end()
        this.loseDialogVisible = true
        return
      }
      //处理点击空白的时候打开周围
      if (this.mineDate[y][x].number === 0) {
        this.getRoundItems(x, y).forEach(item => {
          item.state = 0
        })
      }
      this.leftOpen--
      if (!this.leftOpen) {//成功
        this.end()
        if (this.nowTime < this.record[this.nowLevel]) {
          this.record[this.nowLevel] = this.nowTime
          localStorage.record = JSON.stringify(this.record)
          this.newRecord = true
        }
        this.winDialogVisible = true
      }
    },
    //第一次点击
    firstOpen (x, y) {
      this.setMine(x, y)
      this.setNumber()
    },
    //左右键
    openRoundByFlag (x, y, item) {
      const roundItems = this.getRoundItems(x, y)
      let flagNumber = roundItems.reduce((a, b) => a + Number(b.state === 2), 0)
      if (flagNumber === item.number) {
        roundItems.forEach(item => {
          if (item.state === 1) {
            item.state = 0
          }
        })
      }
    },
    //重新开始
    restart () {
      this.newRecord = false
      this.nowTime = 0
      this.allFlagNumber = 0
      this.leftOpen = this.width * this.height - this.mineNumber
      for (let i = 0; i < this.height; i++) {
        for (let j = 0; j < this.width; j++) {
          this.mineDate[i][j].state = 1
        }
      }
    },
    //结束游戏
    end () {
      clearInterval(this.timer)
      this.timer = null
    },
    //插旗或者取消
    hanldeChangeFlag (n) {
      this.allFlagNumber += n
    }
  }
}
</script>
<style scoped>
.minesweeper {
  display: flex;
  flex-flow: column;
  align-items: center;
}
.mines-row {
  display: flex;
}
.center-box {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.good-box {
  display: flex;
  align-items: center;
}
.title {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 600;
}
</style>