<template>
  <div class="mine-box" @mouseup="handleMouseup">
    <div class="mine-shade" v-show="state">
      <Flag v-show="state===2" class="icon" />
    </div>
    <div class="mine">
      <span v-show="state === 0 && !isMine">{{ number || ' ' }}</span>
      <Mine v-show="state === 0 && isMine" class="icon" />
    </div>
  </div>
</template>
<script>
import Flag from '@/components/SVG/flag'
import Mine from '@/components/SVG/mine'
export default {
  components: { Flag, Mine },
  props: {
    state: {
      //状态 0 打开 1未打开 2 插旗
      type: Number,
      default () {
        return 1
      },
    },
    number: {
      //雷的数量
      type: Number,
      default () {
        return 0
      },
    },
    isMine: {
      //是否是雷
      type: Boolean,
      default () {
        return false
      },
    },
  },
  watch: {
    state (to, from) {
      if (to === 0 && from === 1) {
        this.$emit('open')
      }
    }
  },
  data () {
    return {
      timeStamp: 0,
      lastButton: null,
    }
  },
  methods: {
    handleMouseup (e) {
      if (this.state === 0) {//打开的时候 处理左右键扫雷
        if (this.lastButton === null) {//第一次点击不需要
          this.timeStamp = e.timeStamp
          this.lastButton = e.button
          return
        }
        if (!(this.lastButton === 0 && e.button === 2) && !(this.lastButton === 2 && e.button === 0)) {//排除两个键不是左右的情况
          this.timeStamp = e.timeStamp
          this.lastButton = e.button
          return
        }
        if (e.timeStamp - this.timeStamp < 100) {//必须在100ms之类处理
          this.$emit('openRoundByFlag')
        }
        this.timeStamp = e.timeStamp
        this.lastButton = e.button
        return
      }
      if (this.state === 1) {//未打开的情况
        if (e.button === 0) {//左键
          this.$emit('update:state', 0)
          return
        }
        if (e.button === 2) {//右键位2
          this.$emit('update:state', 2)
          this.$emit('changeFlag', 1)
          return
        }
      }
      if (this.state === 2) {//插旗的情况
        if (e.button === 0) {//左键
          return
        }
        if (e.button === 2) {//右键位2
          this.$emit('update:state', 1)
          this.$emit('changeFlag', -1)

        }
      }
    },
  },
}
</script>
<style scoped>
.mine-box {
  position: relative;
  width: 2rem;
  height: 2rem;
  margin: 0.1rem;
  background-color: #ddd;
}
.mine-shade {
  border-radius: 0.2rem;
  position: absolute;
  background: linear-gradient(to left bottom, #0631e0, #b6bcd6);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mine-shade:hover {
  background: linear-gradient(to left bottom, #b6bcd6, #0631e0);
}
.mine {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon {
  width: 80%;
  height: 80%;
}
</style>